/* Global styles */
.github-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  padding-top: 100px;
  width: 100%;
  transition: transform 0.3s, box-shadow 0.3s;
  background-color: var(--bg-color);
}

.repo-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.repo-card h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.repo-card a {
  text-decoration: none;
  color: #0073e6;
}

.repo-card a:hover {
  text-decoration: underline;
}

.repo-card p {
  font-size: 1em;
  margin-bottom: 10px;
  color: #333;
}

.repo-card .language {
  display: flex;
  align-items: center;
}

.repo-card .language strong {
  margin-right: 5px;
}

.view-repo-button {
  display: inline-block;
  padding: 10px 15px;
  color: #d60000;
  text-align: center;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;
}

.user-rankings {
  margin-top: 0px;
  width: 90%;
  position: relative;
  background-color: rgba(7, 7, 7, 0.2);
  border: 1px solid #b4b4b4;
  border-radius: 10px;
  padding: 20px;
  gap: 100px;
}

.user-rankings h2 {
  font-size: 14px;
  margin-bottom: 20px;
  scroll-behavior: smooth;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.user-rankings span {
  /* transition: 0.1s ease-in-out; */
  background-color: #25409f;
  padding: 7px;
  border-radius: 5px;
}

.navigate-professinals{
  margin: 10px 0 10px 0;
}

.navigate-professinal span {
  background-color: #25409f;
  padding: 10px;
  border-radius: 5px;
  font-size: 20px;
  transition: 0.2s ease-in-out;
}

.ranking-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: start;
  align-items: start;
  text-align: start;
}

.ranking-item {
  display: flex;
  flex-direction: column;
  align-items: start;
  background-color: #040303;
  border-radius: 10px;
  padding: 10px;
  margin: 10px 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.ranking-item:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.profile-infos {
  display: flex;
  align-items: start;
  width: 100%;
}

.user-profile {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-right: 20px;
}

.profile-pic {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid #ddd;
}

.username {
  margin-top: 5px;
  font-weight: bold;
  font-size: 1em;
}

.user-details {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.user-detail-card {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.user-profile-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
}

.dev-score, .rank {
  font-size: 1.2em;
  font-weight: bold;
}

.label {
  font-size: 0.8em;
  color: #666;
}

.see-profile-link {
  display: flex;
  align-items: start;
  margin-top: 10px;
}

.top-skills {
  font-size: 0.9em;
  font-weight: bold;
  color: #555;
}

.see-profile-text {
  margin-left: 10px;
  color: #0073e6;
  cursor: pointer;
  font-weight: bold;
}

.see-profile-text:hover {
  text-decoration: underline;
}

@media (max-width: 1005px) {

  .app-container {
   position: relative;
   top: 0px;
   left: 0px
  }

  .github-profile {
    padding: 0;
    padding-top: 70px;
    overflow-x: hidden;
  }

  .github-profile form {
    font-size: 15px;
    justify-content: start;
    flex-direction: column;
    left: 0;
    align-items: center;
  }

  .brand-name {
    font-size: 50px;
    position: relative;
    top: 50px;
    right: 25px;
  }

  .github-profile input {
    width: 60%;
    margin-bottom: 20px;
  }

  button.learn-more {
    width: auto;
  }

  .button-text {
    opacity: 0;
  }

  .GitHub-Profile-info {
    flex-direction: column;
    align-items: start;
    left: 0;
  }

  .profile {
    padding: 20px;
  }

  .github-profile .profile img {
    width: 100px;
    height: 100px;
  }

  .github-profile .profile h1 {
    font-size: 20px;
  }

  .github-profile .profile p {
    font-size: 14px;
  }

  .details {
    flex-direction: column;
    align-items: center;
  }

  .details p {
    font-size: 16px;
    text-align: center;
  }

  .repos {
    justify-content: center;
  }

  .repo-card {
    width: 100%;
    min-width: 300px;
  }

  .user-rankings {
    padding: 15px;
    margin: 20px;
    width: 88%;
    position: relative;
  }

  .navigate-professinals {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: auto;
    overflow-x: scroll;
    scroll-behavior: smooth;
    position: relative;
    right: 0px;
  }

  .ranking-lists {
    flex-wrap: wrap;
    justify-content: start;
    align-items: start;
    text-align: start;
    gap: 0px;
  }

  .ranking-items {
    margin: 3px;
    width: 95%;
  }

  .profile-infos {
    flex-direction: row;
    align-items: center;
  }

  .user-profile {
    margin-right: 0;
    align-items: start;
  }

  .user-profile img {
    width: 50px;
    height: 50px;
  }

  .username {
    font-size: 0.9em;
  }

  .user-details {
    flex-direction: column;
    align-items: center;
  }

  .user-detail-card {
    flex-direction: row;
    align-items: start;
    justify-content: space-around;
    gap: 0;
  }

  .user-profile-info {
    margin: 0;
  }

  .dev-score, .rank {
    font-size: 1em;
  }

  .see-profile-link {
    justify-content: center;
  }

  .see-profile-text {
    margin-left: 0;
    font-size: 0.8em;
  }

  .languages-card {
    width: 100%;
    max-width: 300px;
    padding: 20px;
  }

  .languages-card h2 {
    font-size: 1.2em;
  }

  .languages-card li {
    font-size: 0.9em;
  }
}